import React, { Component } from 'react';
import { Years, Day31, Month } from "../../../redux/constant";
import INPUTS from "../../common/InputFields";
class Step1 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Fullname: "",
            errors: {
                Fullname: "",
            }
        };
    }
    setStateHandler = val => {
        this.setState({
            errors: val.errors,
            [val.name]: val[val.name]
        })
    }
    render() {
        return (
            <React.Fragment>
                <div className="col-12 Fields">
                    <form>
                        <label htmlFor="dp" className="Image">
                            <div className="Circle">
                                <img src="./img/icon.png" alt="" />
                            </div>
                        </label>
                        <span className="name">Radu Christian</span>
                        <input type="file" name="dp" id="dp" />
                        <INPUTS name="Fullname" label="Full Name" type="text" width="100%" state={this.state} stateHandler={this.setStateHandler} />
                        {this.state.errors.Fullname !== "" ? <span className="error text-danger">{this.state.errors.Fullname}</span> : ""}
                        <div className="date w-100">
                            <label htmlFor="year">Date Of Birth</label>
                            <div className="Inputs w-100">
                                <div className="Year Option">
                                    <select name="year" id="year" placeholder="yyyy"><option defaultValue>yyyy</option>{Years.map((v, k) => { return (<option key={k}>{v}</option>) })}</select>
                                    <i className="fas fa-caret-down"></i>
                                </div>
                                <div className="Month Option">
                                    <select name="Date" id="Date"><option defaultValue>mm</option>{Month.map((v, k) => { return (<option key={k}>{v}</option>) })}</select>
                                    <i className="fas fa-caret-down"></i>
                                </div>
                                <div className="Month Option">
                                    <select name="month" ><option defaultValue>dd</option>{Day31.map((v, k) => { return (<option key={k}>{v}</option>) })}</select>
                                    <i className="fas fa-caret-down"></i>
                                </div>
                            </div>
                        </div>
                        <INPUTS name="Gender" label="Gender" type="text" width="100%" state={this.state} stateHandler={this.setStateHandler} />
                        <button onClick={e => { e.preventDefault(); this.props.StepHandle() }}>Next</button>
                    </form>
                </div>
            </React.Fragment>
        );
    }
}

export default Step1;