import React from 'react';
import { InputValidate } from "../../redux/actions/Inputs";
function Inputs(props) {
    const { name, label, state, stateHandler, types, width, divW, place, icon } = props;
    let errors = state.errors;
    const HandleChanging = e => {
        let errors = InputValidate(e, state);
        stateHandler(errors);
    }
    return (
        <div className="inputs" style={{ width: divW }}>
            <label htmlFor={name}>{label}</label>
            <div className="input-group">
                <input type={types} name={name} id={name} placeholder={place === "" || place === null || place === undefined ? `Enter Your ${label} Here` : place} style={{ width: width }} onChange={HandleChanging} />
                <i className={icon === undefined ? errors[name] === "" ? "fas fa-check" : "fas fa-times" : icon}></i>
            </div>
        </div>
    )
}
export default Inputs;