import React, { Component } from 'react';
import { genres, cover, Slidersettings } from "../../../redux/constant";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import axios from "axios";
class Step2 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Genres: [],
            Movie: [],
            box: 0,
            movies: 0
        };
    }
    componentDidMount = () => {
        axios.get("http://167.71.186.205/api/genres").then(res => {
            console.log(res)
            this.setState({
                Genres: res.data.items,
                Movie: cover
            })
        })
    }
    onMovieSelect = (key, event) => {
        let Node = document.querySelectorAll(".Move");
        let name = this.state.Movie[key].name.split(" ")[0];
        var temp = this.state.movies;
        if (event.target.checked === true) {
            for (let i = 0; i < Node.length; i++) {
                if (name === Node[i].id) {
                    let sibling = Node[i].nextSibling.childNodes[0];
                    sibling.style.backgroundColor = "white"
                }
            }
            temp++;
            this.setState({
                movies: temp
            })
        }
        else {
            for (let i = 0; i < Node.length; i++) {
                if (name === Node[i].id) {
                    let sibling = Node[i].nextSibling.childNodes[0];
                    sibling.style.backgroundColor = "transparent"
                }
            }
            temp--;
            this.setState({
                movies: temp
            })
        }
        // if (temp === 3) {
        //     for (let i = 0; i < Node.length; i++) {
        //         if (Node[i].checked === false) {
        //             Node[i].disabled = true;
        //             Node[i].nextSibling.style.borderColor = "grey"
        //         }
        //     }
        // }
        // else {
        //     for (let i = 0; i < Node.length; i++) {
        //         Node[i].disabled = false;
        //         Node[i].nextSibling.style.borderColor = "white"
        //     }
        // }
    }
    onCheckBoxChange = (key, event) => {
        let tempState = this.state.Genres;
        let inputs = document.querySelectorAll(".checks")
        var temp = this.state.box;
        if (event.target.checked === true) {
            tempState[key].isChecked = true;
            inputs[key].nextSibling.childNodes[0].style.backgroundColor = "#136285";
            temp = temp + 1;
            this.setState({ box: temp })
        }
        else {
            tempState[key].isChecked = false;
            inputs[key].nextSibling.childNodes[0].style.backgroundColor = "transparent";
            temp = temp - 1;
            this.setState({ box: temp })
        }
        if (temp === 3) {
            for (let i = 0; i < inputs.length; i++) {
                if (inputs[i].checked !== true) {
                    inputs[i].disabled = true;
                    inputs[i].nextSibling.style.borderColor = "grey"
                }
            }
        }
        else {
            for (let i = 0; i < inputs.length; i++) {
                inputs[i].disabled = false;
                inputs[i].nextSibling.style.borderColor = "#136285";
            }
        }
    }
    render() {
        const Genreses = this.state.Genres.map((v, k) => {
            return (
                <div className="Containers" key={k}>
                    <div className="Check">
                        <input type="checkbox" className="checks d-none" id={v.name.split(" ")[0]} onChange={this.onCheckBoxChange.bind(this, k)} />
                        <label htmlFor={v.name} style={{ borderColor: "#136285", borderWidth: "2px" }} id="terms"><div style={{ backgroundColor: "transparent" }}></div></label>
                    </div>
                    <div className="Content">
                        <p>{v.name}</p>
                    </div>
                </div>

            )
        })
        const Movie = this.state.Movie.map((v, k) => {
            return (
                <div className="Poster" key={k}>
                    <img src={v.dp} alt="" />
                    <div className="Content w-100">
                        <div className="Check">
                            <input type="checkbox" className="d-none Move" id={v.name.split(" ")[0]} onChange={this.onMovieSelect.bind(this, k)} />
                            <label htmlFor={v.name.split(" ")[0]} className="terms" style={{ borderColor: "#fff", borderWidth: "2px" }} ><div style={{ backgroundColor: "transparent" }}> </div></label>
                        </div>
                        <div className="Title">
                            <span>{v.name}</span>
                            <p>Action,Adventure,Fantasy</p>
                        </div>
                    </div>
                </div>
            )
        })
        return (
            <React.Fragment>
                <div className="col-12 Genres">
                    <h4>CHOOSE 3 GENRES</h4>
                    <div className="GenresContainer">
                        {Genreses}
                    </div>
                    <h4>CHOOSE 3 Movies</h4>
                </div>
                <div className="col-12 Movies">
                    <Slider {...Slidersettings}>
                        {Movie}
                    </Slider>
                </div>
                <div className="col-12 d-flex align-items-center justify-content-center">
                    <button className="next" onClick={e => { e.preventDefault(); this.props.StepHandle() }}>Next</button>
                </div>
            </React.Fragment>
        );
    }
}

export default Step2;