import React, { Component } from 'react';
import { Plans } from "../../../redux/constant/index";
import { Link } from "react-router-dom";
class Step3 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Plans: []
        };
    }
    componentDidMount = () => {
        this.setState({
            Plans: Plans
        })
    }
    render() {
        const Plan = this.state.Plans.map((v, k) => {
            return (
                <div className="col-md-4 col-12">
                    <div className="content">
                        <header>
                            <h4>{v.type}</h4>
                        </header>
                        <div className="Pricing">
                            <div className="text">
                                <span>$</span>
                                <h1>{v.price}</h1>
                                <span>/month</span>
                            </div>
                        </div>
                        <div className="Content">
                            {v.feature.map((val, key) => {
                                return (
                                    <div className="text-group" key={key}>
                                        <p>{val.name}</p>
                                        <span>{val.Allow}</span>
                                    </div>
                                )
                            })}
                        </div>
                        <Link to={v.type === "Premium" ? "/payment" : "/"}>{v.BtnText}</Link>
                    </div>
                </div>
            )
        })
        return (
            <React.Fragment>
                <div className="col-12 Plans">
                    <div className="container">
                        <div className="row pb-md-0 pb-5 justify-content-center">
                            {Plan}
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default Step3;