import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
import STEP1 from "./Step1";
import STEP2 from "./Step2";
import STEP3 from "./Step3";
class Container extends Component {
    constructor(props) {
        super(props);
        this.state = {
            step: 1,
        };
    }
    onStep = () => {

        this.setState(state => {
            return {
                step: ++state.step
            }
        })
    }
    render() {
        return (
            <React.Fragment>
                <div className="container-fluid StepsContainer">
                    <div className="row StepBg">
                        <div className="col-12 Container">
                            <h4>You Are Almost There</h4>
                            <span>Just a few more steps to setup your account</span>
                            <div className="Steps">
                                <div className="StepContent">
                                    <div className="Content" style={{ borderColor: this.state.step === 1 ? "#2AABE4" : "transparent" }}>
                                        <div className="InnerCircle" style={{ backgroundColor: this.state.step === 1 ? "#3A4A51" : "transparent" }}>
                                            <div className="InsideCircle">
                                            </div>
                                        </div>
                                    </div>
                                    <span>COMPLETE YOUR PROFILE</span>
                                </div>
                                <hr style={{ backgroundColor: this.state.step === 1 ? "black" : "#2AABE4", marginLeft: this.state.step === 1 ? "-46px" : "-62px", marginRight: this.state.step === 1 ? "-60px" : this.state.step === 2 ? "-43.5px" : "-60px" }} />
                                <div className="StepContent">
                                    <div className="Content" style={{ borderColor: this.state.step === 2 ? "#2AABE4" : "transparent" }}>
                                        <div className="InnerCircle" style={{ backgroundColor: this.state.step === 2 ? "#3A4A51" : "transparent" }}>
                                            <div className="InsideCircle" style={{ backgroundColor: this.state.step === 2 || this.state.step === 3 ? "white" : "black" }}>
                                            </div>
                                        </div>
                                    </div>
                                    <span>GET TAILORED CONTENT</span>
                                </div>
                                <hr style={{ backgroundColor: this.state.step === 2 || this.state.step === 1 ? "black" : "#2AABE4", marginLeft: this.state.step === 2 ? "-43.5px" : "-60px", marginRight: this.state.step === 3 ? "-36px" : "-55px" }} />
                                <div className="StepContent">
                                    <div className="Content" style={{ borderColor: this.state.step === 3 ? "#2AABE4" : "transparent" }}>
                                        <div className="InnerCircle" style={{ backgroundColor: this.state.step === 3 ? "#3A4A51" : "transparent" }}>
                                            <div className="InsideCircle" style={{ backgroundColor: this.state.step === 3 ? "white" : "black" }}>
                                            </div>
                                        </div>
                                    </div>
                                    <span>CHOSE SUBSCRIPTION</span>
                                </div>
                            </div>
                        </div>
                        {this.state.step === 1 ? <STEP1 StepHandle={this.onStep} /> : ""}
                        {this.state.step === 2 ? <STEP2 StepHandle={this.onStep} /> : ""}
                        {this.state.step === 3 ? <STEP3 /> : ""}
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default withRouter(Container);