import {
    validEmailRegex,
    validCard,
    validPhone
} from "../constant/index";
export const InputValidate = (event, state) => {
    event.persist();
    const {
        name,
        value
    } = event.target;
    let errors = state.errors;
    switch (name) {
        case "username":
            errors.username = value.length < 6 ? "Username Must Be Greater Than 5 Character!" : "";
            break;
        case "creditCardNumber":
            errors.creditCardNumber = validCard.test(value) ? "" : "Please Enter Valid Card Number";
            break;
        case 'email':
            errors.email =
                validEmailRegex.test(value) ?
                '' :
                'Please Enter Valid Email!';
            break;
        case 'password':
            errors.password =
                value.length < 6 ?
                'Password must be 6 characters long!' :
                '';
            break;
        case "Fullname":
            errors.Fullname = value.length < 6 ? "Username Must Be Greater Than 5 Character!" : "";
            break;
        case "phoneNumber":
            errors.phoneNumber =
                validPhone.test(value) ? "" : "Please Enter Valid Phone Number!"
            break;
        default:
            errors[name] = value.length >= 1 ? "" : "Fill The Field!";
            break;
    }
    return {
        errors,
        [name]: value,
        name: name
    };
}